@import '../../../../includes/assets/css/consts.scss';
@import '../base/mixins';

.dpit-theme-basic {

	.dpit-tooltip-ref {
		//perspective: 500;
	}


	/*.dpit-bullets {
		ul {
			transform: skew(-10deg, 0deg);
			@include transitionFast(transform);
		}
	}
*/




	.dpit-button {
		background-color: rgba(var(--accent-color), 0.4);
		color: rgba(var(--accent-contrast-inv-color), 0.9);
		font-weight: 600;
		text-transform: uppercase;

		&:not(.dpit-disabled) {

			&:focus {
				color: rgb(var(--accent-contrast-color));
				background-color: rgb(var(--accent-color), 0.8);
			}

			@include atHoverAbleDev {

				&:active,
				&:hover,
				&.hovered {
					color: rgb(var(--accent-contrast-color));
					background-color: rgba(var(--accent-color));
				}
			}

		}
	}

	&.dpit-light-accent {
		.dpit-button {
			color: rgba(var(--accent-contrast-inv-color), 0.8);
		}

	}


}